export default class IntlFlightOrder { // 国际航班订单类
  constructor(details) {
    if (details) {
      for (let key of Object.keys(details)) {
        this[key] = JSON.parse(JSON.stringify(details[key]))
      }
    }
    this.SearchParams = details && details.SearchParams ? details.SearchParams : {
      Segments: [{
        DepartObject: {}, // 前端用于显示, 自定义的出发对象
        ArrivalObject: {}, // 前端用于显示, 自定义的到达对象
        DepartureTime: this.beTime(new Date().setDate(new Date().getDate() + 1)),
        DepartureAirportCode: '',
        ArrivalAirPortCode: ''
      }],
      DirectFlight: false,
      FlightClass: '',
      AdultCount: 1,
      ChildCount: 0,
      InfantCount: 0,
      AirLine: ''
    } // 查询国际航班参数
    this.SelectedSegmentIDs = details && details.SelectedSegmentIDs ? details.SelectedSegmentIDs : '' // 选择的航段ID列表
    this.SelectedFareInfoID = details && details.SelectedFareInfoID ? details.SelectedFareInfoID : '' // 选择的价格信息ID
    this.TravelType = details && details.TravelType ? details.TravelType : '' // 旅游类型 0:因公 1:因私
    this.AuthorizationCode = details && details.AuthorizationCode ? details.AuthorizationCode : '' // 授权码
    this.Purpose = details && details.Purpose ? details.Purpose : '' // 差旅目的
    this.Passengers = details && details.Passengers ? details.Passengers : [] // 常旅客
    this.CustomItem = details && details.CustomItem ? details.CustomItem : {} // 自定义项(所有乘客一样)
    this.Contacts = details && details.Contacts ? details.Contacts : [] // 联系人信息
    this.PayType = details && details.PayType ? details.PayType : null // 支付类型
    this.AppointVettingPersonID = details && details.AppointVettingPersonID ? details.AppointVettingPersonID : '' // 选择的指定审批人ID
    this.AppointVettingPersonName = details && details.AppointVettingPersonName ? details.AppointVettingPersonName : '' // 选择的指定审批人姓名
    this.TotalPrice = details && details.TotalPrice ? details.TotalPrice : 0 // 总金额
    /** 其他自定义参数, 便于下单流程中使用 */
    this.RequestSegmentIndex = details && details.RequestSegmentIndex ? details.RequestSegmentIndex : 1
    this.SelectedSegment = details && details.SelectedSegment ? details.SelectedSegment : []
    this.PageBtnInfo = details && details.PageBtnInfo ? details.PageBtnInfo : {
      IsAllowCancel: false,
      IsAllowPay: false,
      IsAllowPayAndSubmit: false,
      IsAllowSubmit: false
    }
  }
  beTime(ms, format) {
    let isDate = ms instanceof Date
    let date = ''
    if (isDate) {
      date = new Date(ms)
    } else {
      let val = ms.toString().length === 10 ? ms * 1000 : ms
      date = new Date(val)
    }
    var fmt = format !== undefined ? format : 'yyyy-MM-dd' // yyyy-MM-dd hh:mm
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + ''
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
      }
    }
    return fmt
  }
}



