import Vue from 'vue'
export default class Passenger { // 常旅客类 + 乘客
  constructor(details) {
    // 常旅客
    this.ID = details && details.ID ? details.ID : (details && details.UpID ? details.UpID : '') // 主键ID
    this.Credentials = details && details.Credentials ? this.fillInCredentials(details.Credentials) : this.fillInCredentials() // 证件信息列表
    this.Gender = (details === undefined || details.Gender === undefined || details.Gender === null) ? true : details.Gender // 性别 默认true，男
    this.Nation = details && details.Nation ? details.Nation : null // 国籍
    this.NationCode = details && details.NationCode ? details.NationCode : '' // 国籍二字码
    this.NationName = details && details.NationName ? details.NationName : '' // 国籍名称
    // ====================================================================================================
    // 乘客
    this.UpID = details && details.UpID ? details.UpID : (details && details.ID ? details.ID : '') // 员工ID 同 this.ID
    this.Credential = details && details.Credential ? this.initCredential(details.Credential) : this.initCredential() // 证件信息
    this.IsSendBookSms = details && details.IsSendBookSms ? details.IsSendBookSms : false // 是否发送预订短信
    this.IsSendBookEmail = details && details.IsSendBookEmail ? details.IsSendBookEmail : false // 是否发送预订邮箱
    this.IsSendIssuedSms = details && details.IsSendIssuedSms ? details.IsSendIssuedSms : false // 是否发送出票短信
    this.IsSendIssuedEmail = details && details.IsSendIssuedEmail ? details.IsSendIssuedEmail : false // 是否发送出票邮箱
    this.IsSendBookWechat = details && details.IsSendBookWechat ? details.IsSendBookWechat : false // 是否给乘客发送预订微信通知
    this.IsSendIssuedWechat = details && details.IsSendIssuedWechat ? details.IsSendIssuedWechat : false // 是否给乘客发送出票微信通知
    this.IsSendConfirmSms = details && details.IsSendConfirmSms ? details.IsSendConfirmSms : false // 是否发送确认短信
    this.IsSendConfirmEmail = details && details.IsSendConfirmEmail ? details.IsSendConfirmEmail : false // 是否发送确认邮件
    this.Insurances = details && details.Insurances ? details.Insurances : [] // 保险
    this.openDelete = details && details.openDelete ? details.openDelete : false // 是否处于待删除状态
    // this.isFrequentContact = details && details.isFrequentContact ? details.isFrequentContact : false // 是否已经添加为常用联系人
    // ====================================================================================================
    // 共用
    this.UpType = details && details.UpType ? details.UpType : 2 // 类别(UP和NUP)
    this.Name = details && details.Name ? details.Name : '' // 乘客姓名(中文名或拼接后的英文名)
    this.ChName = details && details.ChName ? details.ChName : '' // 中文名
    this.ENnameFirst = details && details.ENnameFirst ? details.ENnameFirst : '' // 英文首姓名
    this.ENnameMiddle = details && details.ENnameMiddle ? details.ENnameMiddle : '' // 英文中间姓名
    this.ENnameLast = details && details.ENnameLast ? details.ENnameLast : '' // 英文后姓名
    this.Birthday = details && details.Birthday ? details.Birthday : '' // 出生日期
    this.Email = details && details.Email ? details.Email : '' // 邮箱
    this.Mobile = details && details.Mobile ? details.Mobile : '' // 手机号
    this.Department = details && details.Department ? details.Department : null // 部门信息
    this.CostCenter = details && details.CostCenter ? details.CostCenter : null // 成本中心信息
    this.BusinessUnit = details && details.BusinessUnit ? details.BusinessUnit : null // BU
    this.UPRole = details && details.UPRole ? details.UPRole : '' // 员工角色
    this.PersonType = details && details.PersonType ? details.PersonType : '' // 员工类型 会员类型
    this.PassengerType = details && details.PassengerType ? details.PassengerType : 1 // 自然人类型, 成人
    this.TravelType = details && details.TravelType ? details.TravelType : 0 // 因公因私
    this.TravelerEntry = details && details.TravelerEntry ? details.TravelerEntry : 0 // 0从订单填写页入口;1从常旅客管理页入口
    // this.roomIndex = details && details.roomIndex ? details.roomIndex : -1 // 所在房间号码
    this.GuestToRoomNo = details && details.GuestToRoomNo >= 0 ? details.GuestToRoomNo : -1 // 所在房间号码
    this.GuestType = details && details.GuestType ? details.GuestType : 0
    this.TravelCardList = details && details.TravelCardList ? details.TravelCardList : [] // 常旅客卡
  }
  fillInCredentials(currentValue = []) {
    let res = [
      {ID: '', CredentialType: 1, CredentialName: Vue.prototype.$t('base.IDTypeNames.IDCard'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 2, CredentialName: Vue.prototype.$t('base.IDTypeNames.Passport'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 3, CredentialName: Vue.prototype.$t('base.IDTypeNames.StudentCard'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 4, CredentialName: Vue.prototype.$t('base.IDTypeNames.MilitaryID'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 5, CredentialName: Vue.prototype.$t('base.IDTypeNames.HomeReturnPermit'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 6, CredentialName: Vue.prototype.$t('base.IDTypeNames.ForeignersPermanentResidencePermit'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 7, CredentialName: Vue.prototype.$t('base.IDTypeNames.EntryExitPassForHongKongAndMacau'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 8, CredentialName: Vue.prototype.$t('base.IDTypeNames.EntryExitPassForTaiwan'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 9, CredentialName: Vue.prototype.$t('base.IDTypeNames.InternationalSeafarersCard'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 10, CredentialName: Vue.prototype.$t('base.IDTypeNames.TaiwanCompatriotPermit'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 11, CredentialName: Vue.prototype.$t('base.IDTypeNames.Others'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 12, CredentialName: Vue.prototype.$t('base.IDTypeNames.HongKongMacaoAndTaiwanResidentsResidencePermit'), CredentialNo: '', EffectiveDate: '', IsDefault: false}
    ]
    // 凭借-排序-去重
    res = [this.Credential, ...currentValue, ...res].filter(v => v)
    let maplist = new Map()
    res = res.filter(a => !maplist.has(a.CredentialType) && maplist.set(a.CredentialType, 1))
    res = res.sort((a, b) => { return a['CredentialType'] - b['CredentialType'] })
    // let maplist = []
    // res = res.filter(a => !(maplist.indexOf(a.CredentialType) >= 0) && maplist.push(a.CredentialType))
    return res
  }
  initCredential(currentValue) {
    let list = [currentValue, ...this.Credentials].filter(v => v)
    let CredentialNos = list.map(v => v.CredentialNo).filter(v => v)
    let index = 0
    if (CredentialNos.length > 0) {
      index = list.map(v => v.CredentialNo).indexOf(CredentialNos[0])
    }
    return list[index]
  }
}



