import FlightOrder from 'common/model/flight/flightOrder'
import IntlFlightOrder from 'common/model/intlFlight/intlFlightOrder'
import HotelOrder from 'common/model/hotel/hotelOrder'
import TrainOrder from 'common/model/train/trainOrder'
const state = {
  direction: 'forward', // 路由跳转方向
  flightOrder: new FlightOrder(), // 机票订单详情
  flightRight: {}, // 账户机票权限
  flightOrderInit: {}, // 机票订单初始化信息
  intlFlightOrder: new IntlFlightOrder(), // 国际机票
  unHandleVettingCount: 0, // 未审核消息提醒条数
  personalCenterData: {}, // 个人中心数据
  isMiniprogram: false, // 是否在小程序中
  isWechatBrowser: false, // 是否在微信浏览器中
  hotelOrder: new HotelOrder(), // 酒店订单详情
  hotelRight: {}, // 账户酒店权限
  trainOrder: new TrainOrder(),
  trainRight: {},
  trainChange: {}, // 火车票改期信息
  interHotelOrder: new HotelOrder(), // 国际酒店订单详情
  interHotelRight: {} // 国际酒店账户酒店权限
}
export default state



