export default class trainOrder {
  constructor (details) {
    if (details) {
      for (let key of Object.keys(details)) {
        this[key] = JSON.parse(JSON.stringify(details[key]))
      }
    }
    this.OriginalOrderID = details && details.OriginalOrderID ? details.OriginalOrderID : null // 原订单ID
    this.TravelType = details && details.TravelType ? details.TravelType : null // 旅游类型
    this.DepartStationCode = details && details.DepartStationCode ? details.DepartStationCode : '' // 出发城市Code
    this.DepartStationName = details && details.DepartStationName ? details.DepartStationName : '' // 出发城市名称
    this.ArrivalStationCode = details && details.ArrivalStationCode ? details.ArrivalStationCode : '' // 到达城市Code
    this.ArrivalStationName = details && details.ArrivalStationName ? details.ArrivalStationName : '' // 到达城市名称
    this.DepartDate = this.beTime(new Date().setDate(new Date().getDate() + 1));
    this.ArrivalDate = ''
    // this.ArrivalDate = this.beTime(new Date().setDate(new Date().getDate() + 2));
    this.isHighSpeed = false;
    this.GoSelectedRoute = details && details.GoSelectedRoute ? details.GoSelectedRoute : {TrainCode: ''};
    this.BackSelectedRoute = details && details.BackSelectedRoute ? details.BackSelectedRoute : {TrainCode: ''};
    // book信息
    this.Passengers = details && details.Passengers ? details.Passengers : [] // 乘客列表
    this.CustomItem = details && details.CustomItem ? details.CustomItem : {} // 自定义项(所有乘客一样)
    this.Contacts = details && details.Contacts ? details.Contacts : [] // 联系人信息
    this.PayType = details && details.PayType ? details.PayType : null // 支付类型
    this.Purpose = details && details.Purpose ? details.Purpose : '' // 差旅目的
    this.AuthorizationCode = details && details.AuthorizationCode ? details.AuthorizationCode : '' // 授权码
    this.AppointVettingPersonID = details && details.AppointVettingPersonID ? details.AppointVettingPersonID : '' // 选择的指定审批人ID
    this.AppointVettingPersonName = details && details.AppointVettingPersonName ? details.AppointVettingPersonName : '' // 选择的指定审批人姓名
    this.ViolationRankName = details && details.ViolationRankName ? details.ViolationRankName : '' // 违反差旅政策姓名
    this.ViolationRankReason = details && details.ViolationRankReason ? details.ViolationRankReason : '' // 违反差旅政策原因
    this.VettingProcessList = details && details.VettingProcessList ? details.VettingProcessList : [] // 审批流
    this.IsAcptStanding = details && details.IsAcptStanding ? details.IsAcptStanding : false
    this.DeliveryType = details && details.DeliveryType ? details.DeliveryType : 3
    this.DeliveryAddress = details && details.DeliveryAddress ? details.DeliveryAddress : ''
    /** 前端用于显示, 自定义的字段 */
    this.DepartObject = details && details.DepartObject ? details.DepartObject : '' // 出发对象
    this.ArrivalObject = details && details.ArrivalObject ? details.ArrivalObject : '' // 到达对象
    this.Ids = details && details.Ids ? details.Ids : []
    /*******************************************************************************************************************************************************/
  }
  beTime(ms, format) {
    let isDate = ms instanceof Date
    let date = ''
    if (isDate) {
      date = new Date(ms)
    } else {
      let val = ms.toString().length === 10 ? ms * 1000 : ms
      date = new Date(val)
    }
    var fmt = format !== undefined ? format : 'yyyy-MM-dd' // yyyy-MM-dd hh:mm
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + ''
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
      }
    }
    return fmt
  }
}



