export default class HotelOrder { // 酒店订单类
  constructor(details) {
    if (details) {
      for (let key of Object.keys(details)) {
        this[key] = JSON.parse(JSON.stringify(details[key]))
      }
    }
    this.OrderID = details && details.OrderID ? details.OrderID : null // 订单ID
    this.CityName = details && details.CityName ? details.CityName : '' // 城市名称
    this.CityID = details && details.CityID ? details.CityID : '' // 城市ID
    this.HotelID = details && details.HotelID ? details.HotelID : '' // 酒店ID
    this.CheckInDate = details && details.CheckInDate ? details.CheckInDate : this.beTime(new Date()) // 入住日期
    this.CheckOutDate = details && details.CheckOutDate ? details.CheckOutDate : this.beTime(new Date().setDate(new Date().getDate() + 1)) // 离店日期
    this.RoomID = details && details.RoomID ? details.RoomID : '' // 房型ID
    this.RatePlanID = details && details.RatePlanID ? details.RatePlanID : '' // 产品价格计划ID
    this.RoomAmount = details && details.RoomAmount ? details.RoomAmount : 1 // 房间数
    this.CustomItem = details && details.CustomItem ? details.CustomItem : {} // 自定义项(所有乘客一样)
    this.Contacts = details && details.Contacts ? details.Contacts : [] // 联系人信息
    this.PayType = details && details.PayType ? details.PayType : null // 支付类型
    this.Purpose = details && details.Purpose ? details.Purpose : '' // 差旅目的
    this.AuthorizationCode = details && details.AuthorizationCode ? details.AuthorizationCode : '' // 授权码
    this.AppointVettingPersonID = details && details.AppointVettingPersonID ? details.AppointVettingPersonID : '' // 选择的指定审批人ID
    this.AppointVettingPersonName = details && details.AppointVettingPersonName ? details.AppointVettingPersonName : '' // 选择的指定审批人姓名
    this.ViolationRankName = details && details.ViolationRankName ? details.ViolationRankName : '' // 违反差旅政策姓名
    this.ViolationRankReason = details && details.ViolationRankReason ? details.ViolationRankReason : '' // 违反差旅政策原因
    this.Guests = details && details.Guests ? details.Guests : [] // 入住人列表
    this.ArrivalTime = details && details.ArrivalTime ? details.ArrivalTime : '18:00' // 到达时间(HH：mm)
    this.SpecificRequirements = details && details.SpecificRequirements ? details.SpecificRequirements : '' // 特殊需求
    this.TravelType = details && details.TravelType ? details.TravelType : null // 因公因私
    this.OrderType = details && details.OrderType ? details.OrderType : null // 订单类型
    this.VettingProcessList = details && details.VettingProcessList ? details.VettingProcessList : [] // 审批流列表
    this.filters = details && details.filters ? details.filters : [] // 筛选条件
    // 国际酒店
    this.AdultAmount = details && details.AdultAmount ? details.AdultAmount : 0
    this.NationalityName = details && details.NationalityName ? details.NationalityName : '中国'
    this.NationalityCode = details && details.NationalityCode ? details.NationalityCode : 'CN'
    /** 前端用于显示, 自定义的字段 */
    this.City = details && details.City ? details.City : null
    this.Hotel = details && details.Hotel ? details.Hotel : null
    /*******************************************************************************************************************************************************/
  }
  beTime(ms, format) {
    let isDate = ms instanceof Date
    let date = ''
    if (isDate) {
      date = new Date(ms)
    } else {
      let val = ms.toString().length === 10 ? ms * 1000 : ms
      date = new Date(val)
    }
    var fmt = format !== undefined ? format : 'yyyy-MM-dd' // yyyy-MM-dd hh:mm
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + ''
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
      }
    }
    return fmt
  }
}



