import Passenger from '../passenger/passenger'
export default class FlightOrder { // 航班订单类
  constructor(details) {
    if (details) {
      for (let key of Object.keys(details)) {
        this[key] = JSON.parse(JSON.stringify(details[key]))
      }
    }
    this.TravelID = details && details.TravelID ? details.TravelID : '' // 差旅订单号
    this.Passengers = details && details.Passengers ? this.setPassenger(details.Passengers) : [] // 常旅客
    this.CustomItem = details && details.CustomItem ? details.CustomItem : null // 自定义项(所有乘客一样)
    this.Contacts = details && details.Contacts ? details.Contacts : [] // 联系人信息
    this.DeliveryInfo = details && details.DeliveryInfo ? details.DeliveryInfo : null // 配送信息
    this.PayType = details && details.PayType ? details.PayType : 0 // 支付类型
    this.PayStatus = details && details.PayStatus ? details.PayStatus : 0 // 支付状态 0未支付1已支付
    this.Purpose = details && details.Purpose ? details.Purpose : '' // 差旅目的
    this.AppointVettingPersonID = details && details.AppointVettingPersonID ? details.AppointVettingPersonID : '' // 选择的指定审批人ID
    this.AppointVettingPersonName = details && details.AppointVettingPersonName ? details.AppointVettingPersonName : '' // 选择的指定审批人姓名
    this.TrvID = details && details.TrvID ? details.TrvID : '' // 差旅预订号
    this.TravelPlansID = details && details.TravelPlansID ? details.TravelPlansID : '' // 计划行程ID
    this.OriginBookingID = details && details.OriginBookingID ? details.OriginBookingID : null // 原订单ID(只有升舱改期的订单才会有值)
    this.OriginPnrID = details && details.OriginPnrID ? details.OriginPnrID : null // 原pnr ID(只有升舱改期的订单才会有值)
    this.SegmentIDList = details && details.SegmentIDList ? details.SegmentIDList : [] // 改期才需要的航段ID列表
    this.ViolationFloorPirceReason = details && details.ViolationFloorPirceReason ? details.ViolationFloorPirceReason : '' // 违反最低价原因
    this.ViolationCabinRankName = details && details.ViolationCabinRankName ? details.ViolationCabinRankName : '' // 违反政策姓名
    this.ViolationCabinRankReason = details && details.ViolationCabinRankReason ? details.ViolationCabinRankReason : '' // 违反舱位等级的原因
    this.CompanyPayType = details && details.CompanyPayType ? details.CompanyPayType : '' // 页面上传入，针对奔驰一家单位增加的
    this.AuthorizationCode = details && details.AuthorizationCode ? details.AuthorizationCode : '' // 授权码
    this.GoSelectedFlight = details && details.GoSelectedFlight ? details.GoSelectedFlight : {
      FlightNo: '',
      CabinID: '',
      DepartDate: this.beTime(new Date().setDate(new Date().getDate() + 1)),
      FlightDetails: {},
      CabinDetails: {}
    } // 去程选择的航班信息
    this.BackSelectedFlight = details && details.BackSelectedFlight ? details.BackSelectedFlight : {
      FlightNo: '',
      CabinID: '',
      DepartDate: '',
      // DepartDate: this.beTime(new Date().setDate(new Date().getDate() + 2)),
      FlightDetails: {},
      CabinDetails: {}
    } // 返程选择的航班信息
    this.GoLowestPriceFlights = details && details.GoLowestPriceFlights ? details.GoLowestPriceFlights : []// 去程*最低价*航班信息
    this.BackLowestPriceFlights = details && details.BackLowestPriceFlights ? details.BackLowestPriceFlights : [] // 返程*最低价*航班信息
    /** 前端用于显示, 自定义的字段 */
    this.DepartObject = details && details.DepartObject ? details.DepartObject : '' // 出发对象
    this.ArrivalObject = details && details.ArrivalObject ? details.ArrivalObject : '' // 到达对象
    /*********************************************************************************************************************************/
    this.DepartCode = details && details.DepartCode ? details.DepartCode : '' // 出发三字码
    this.ArrivalCode = details && details.ArrivalCode ? details.ArrivalCode : '' // 到达三字码
    this.DepartName = details && details.DepartName ? details.DepartName : '' // 出发名称
    this.ArrivalName = details && details.ArrivalName ? details.ArrivalName : '' // 到达名称
    this.DepartCityCode = details && details.DepartCityCode ? details.DepartCityCode : '' // 出发城市Code
    this.ArrivalCityCode = details && details.ArrivalCityCode ? details.ArrivalCityCode : '' // 到达城市Code
    this.DepartCityName = details && details.DepartCityName ? details.DepartCityName : '' // 出发城市名称
    this.ArrivalCityName = details && details.ArrivalCityName ? details.ArrivalCityName : '' // 到达城市名称
    this.DepartGeoType = details && details.DepartGeoType ? details.DepartGeoType : '' // 去程地理位置类型(0城市 1机场)
    this.ArrivalGeoType = details && details.ArrivalGeoType ? details.ArrivalGeoType : '' // 返程地理位置类型(0城市 1机场)
    this.TravelType = details && details.TravelType ? details.TravelType : null // 旅游类型
    this.TotalPrice = details && details.TotalPrice ? details.TotalPrice : 0 // 总价
    this.VettingProcessList = details && details.VettingProcessList ? details.VettingProcessList : [] // 审批流列表
    this.SettlementType = details && details.SettlementType ? details.SettlementType : '' // 结算方式
    this.CurrentUserID = details && details.CurrentUserID ? details.CurrentUserID : '' // 当前登录用户ID
    this.DisplayConfig = details && details.DisplayConfig ? details.DisplayConfig : {
      IsDisplayCustomItem: false, // 是否显示自定义项
      IsDisplayPurpose: false, // 是否显示出差目的
      IsDisplayAuthorizationCode: false, // 是否显示授权码
      IsDisplayBusinessUnit: false, // 是否显示BU
      IsDisplayDepartment: false, // 是否显示部门
      IsDisplayCostCenter: false // 是否显示成本中心
    } // 单位显示/隐藏配置项
    /** 其他自定义参数, 便于下单流程中使用 */
    this.PageBtnInfo = details && details.PageBtnInfo ? details.PageBtnInfo : {
      IsAllowCancel: false,
      IsAllowPay: false,
      IsAllowPayAndSubmit: false,
      IsAllowSubmit: false
    }
  }
  beTime(ms, format) {
    let isDate = ms instanceof Date
    let date = ''
    if (isDate) {
      date = new Date(ms)
    } else {
      let val = ms.toString().length === 10 ? ms * 1000 : ms
      date = new Date(val)
    }
    var fmt = format !== undefined ? format : 'yyyy-MM-dd' // yyyy-MM-dd hh:mm
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + ''
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
      }
    }
    return fmt
  }
  setPassenger(list) {
    return list.forEach((element, index) => {
      list[index] = new Passenger(element)
    })
  }
}



