import * as types from './mutation-types'
import Vue from 'vue'
import {kpiFlightGetFlightIndexInitInfo, kpiUserGetUserCenterInitInfo, kpiHotelGetHotelIndexInitInfo, kpiTrainGetHotelIndexInitInfo, kpiHotelGetInterHotelIndexInitInfo} from 'api'
export const dispatchFlightRight = function({commit, state}) {
  let promise = new Promise(function(resolve, reject) {
    if (!Vue.prototype.noValue(JSON.stringify(state.flightRight))) {
      resolve(state.flightRight)
    } else {
      kpiFlightGetFlightIndexInitInfo({}).then((res) => {
        console.log('机票权限查询页面：', res)
        commit(types.SET_FLIGHT_RIGHT, res.ResultData)
        resolve(state.flightRight)
      }).catch((err) => {
        reject(err)
      })
    }
  })
  return promise
}
