import * as types from './mutation-types'
import FlightOrder from 'common/model/flight/flightOrder'
import IntlFlightOrder from 'common/model/intlFlight/intlFlightOrder'
import HotelOrder from 'common/model/hotel/hotelOrder'
import TrainOrder from 'common/model/train/trainOrder'

const matutions = {
  [types.UPDATE_DIRECTION](state, payload) {
    state.direction = payload.direction
  },
  [types.SET_FLIGHT_ORDER](state, payload) {
    state.flightOrder = payload === undefined ? new FlightOrder() : payload
  },
  [types.SET_FLIGHT_RIGHT](state, payload) {
    state.flightRight = payload
  },
  [types.SET_FLIGHT_ORDER_INIT](state, payload) {
    state.flightOrderInit = payload
  },
  [types.SET_INTL_FLIGHT_ORDER](state, payload) {
    state.intlFlightOrder = payload === undefined ? new IntlFlightOrder() : payload
  },
  [types.SET_UN_HANDLE_VETTING_COUNT](state, payload) {
    state.unHandleVettingCount = payload
  },
  [types.SET_PERSONAL_CENTER_DATA](state, payload) {
    state.personalCenterData = payload
  },
  [types.SET_IS_MINIPROGRAM](state, payload) {
    state.isMiniprogram = payload
  },
  [types.SET_IS_WECHAT_BROWSER](state, payload) {
    state.isWechatBrowser = payload
  },
  [types.SET_HOTEL_ORDER](state, payload) {
    state.hotelOrder = payload === undefined ? new HotelOrder() : payload
  },
  [types.SET_HOTEL_RIGHT](state, payload) {
    state.hotelRight = payload
  },
  [types.SET_INTER_HOTEL_ORDER](state, payload) {
    state.interHotelOrder = payload === undefined ? new HotelOrder() : payload
  },
  [types.SET_INTER_HOTEL_RIGHT](state, payload) {
    state.interHotelRight = payload
  },
  [types.SET_TRAIN_ORDER](state, payload) {
    state.trainOrder = payload === undefined ? new TrainOrder() : payload
  },
  [types.SET_TRAIN_RIGHT](state, payload) {
    state.trainRight = payload
  },
  [types.SET_TRAIN_CHANGE](state, payload) {
    state.trainChange = payload
  }
}
export default matutions



